import uniIcon from "@/components/uni/uni-icon/uni-icon.vue"
export default {
	data() {
		return { 
			stop: false,
			miao: 0,
			mobile:'',
			code:'',
			status:'0',
			psd:'',
			new_psd:''
		};
	},
	components: {uniIcon},
	onLoad() {
		
	},
	methods: { 
		back(){
			uni.navigateBack()
		},
		change_sta(){
			this.status = 1
		},
		edit_psd(){
			const mobile=this.mobile
			const code=this.code
			const new_psd = this.new_psd
			if(mobile.length!=11){
				this.$api.msg('手机号或验证码错误')
				return;
			}
			uni.showToast({
			    title: '修改成功',
			    duration: 2000
			});
			setTimeout(function(){
				uni.navigateBack()
			},2000)
		},
		
		login(){
			const mobile=this.mobile
			const code=this.code
			const psd = this.psd
			if(mobile.length!=11){
				this.$api.msg('手机号或验证码错误')
				return;
			}
			// uni.setStorageSync('token',res.token)
			uni.switchTab({
				url:'/pages/index/index'
			}) 
		},
		send(){
			const mobile=this.mobile
			if(mobile.length!=11){
				this.$api.msg('手机号错误')
				return; 
			}
		},
		settime(smiao) {
			const that = this
			const miao = that.miao
			if (miao == 0) {
				that.miao=0
				this.stop = false
			} else {
				setTimeout(() => {
					smiao--
					this.miao = smiao
					that.settime(smiao)
				}, 1000)
			}
		
		}
	}
}
